.page2 {
    display: flex;
    justify-content: center;
    flex-direction: row;
    align-items: center;
    position:initial;
}
#room1 {
    background-size: contain;
    position:relative;
    width: 55%;
    height: 93%;
}

.section {
    width: 29%;
    padding-left: 1%;
    height: 90%;
    color: white;
}
.section-title {
    font-size: 5vh;
    font-weight: 500;
}
.section-subtitle {
    font-style: italic;
    margin:1vw;
}
.section-body {
    margin:1vw;
    margin-top: 2vw;
}

#monitor-container {
    width:100%; height:100%;
    position:absolute;
    top:0; left:0;
    align-items: center;
    transition-duration: 1s;
    z-index: 10;
}
#monitor-background {
    width:100%; height:100%;
    background: rgba(200,200,200,.2);
}
#monitor {
    width: 86%; height: 86%;
    position: absolute;
    top: 7%; left: 7%;
    background-size: contain;
}
