.sky {
    position: absolute; top:0;
    height:90%;
    width:100%;
    z-index: 0;
}
.ground {
    position: absolute; bottom:0;
    height:20%;
    width:100%;
    z-index: 5;
}
.mntn1 {
    position: absolute;
    bottom: -22%;
    right: -2%;
    background-size: contain;
    width:100%; height:100%;
    z-index: 3;
}
.mntn2 {
    position: absolute;
    bottom: -27%;
    left: -8%;
    background-size: contain;
    width:100%; height:100%;
    z-index: 2;
}
.mntn3 {
    position: absolute; bottom:-9%;
    background-size: contain;
    width:100%; height:100%;
    z-index: 1;
}
#moon {
    position: absolute;
    top: 6%;
    left: 10%;
    background-size: contain;
    width: 5vw;
    height: 5vw;
    z-index: 5;
}
#hut {
    position: absolute;
    left: 42.5%;
    top: 50%;
    width: 15%;
    height: 37%;
    background-size: contain;
    z-index: 6;
}
#main-title {
    position: absolute;
    top: 27%;
    color: white;
    opacity: .95;
    left: 10%;
    z-index: 1;
}
#title {
    font-size: 5vw;
}
#subtitle {
    font-size: 2vw;
}
